import Product from "../Product";
import { useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { useParams, Link } from "react-router-dom";
import ProductDetail from "../ProductDetail";
const ProductList = () => {
  const { category,productId } = useParams();

  const [state, setState] = useState([]);
  const Desserts = useSelector((state) => state.catalog);
  const getList1 = async() => { 
    const response = await fetch("/api",{method:"GET"});
    const desserts = await response.json();
   return desserts ;
  };

  //getList1();
  const getList = () => { 
   return new Promise(function(myResolve, myReject) {
     
    setTimeout(function(){
      myResolve(Desserts);  // when successful
    
  }, 1000);
    
      });
  };

  useEffect(() => {
    getList().then((data) => {
      const fetchList = data;
      setState(fetchList[category]);
    
    });
  }, [category]);
 
  return (
    <div id="allDishes">
      {!productId && state.length !== 0
        ? state.map((productObj) => (
            <Link to={`${category}/${productObj.id}`} key={productObj.id}>
            <Product key={productObj.id} product={productObj} />
            </Link>
          ))
        : productId?<ProductDetail id={productId} />:"Loading Data..."}

       
    </div>
  );
};

export default ProductList;
